.hero-container{
    background: turquoise;
    height: 90vh;
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}
.intro{
    color: peachpuff;
    background-color: rgba(54, 52, 75, 0.8);
    width: 100%;
    height: 600px;
    text-align: center;
    margin: 100px 0px 90px;
}

p{
    padding: 0px 20vw;
    text-align: center;
    font-size: 1.2rem;
}

.buttons{
    display: flex;
    flex-direction: row;
}

.btn{
    margin: 50px;
}

.type {
    display:inline-flex;
  }
  
  .type span {
    word-break: break-all;
    height: 1.2em;
    width:0%;
    overflow: hidden;
    animation:t 2s linear infinite alternate;
  }
  .type span:before {
    content:" ";
    display:inline-block;
  }
  
  @keyframes t{
    90%,100% {width:100%}
  }
  
  
  /**/
  
  h1 {
    display:table;
    margin:auto;
  }
  
  p {
    font-size:25px;
  }
  
  p + p {
    font-style:italic;
  }

  @keyframes t{
    90%,100% {width:100%}
  }
  
  @keyframes c{
    0%,100%{box-shadow:5px 0 0 #0000}
    50%    {box-shadow:5px 0 0 red  }
  }

  #hello{
    font-size: 3rem;
    color: beige;
  }
  #name{
    color: rgb(210, 117, 104);
    font-size: 3rem;
  }

  #helloName{
    padding-top: 30px;
  }
